<template>
<h2 hidden>{{state.goalid = $route.params.id }}</h2>
<div class="row">
  <div class="col-lg-3 col-md-12"></div>
  <div class="card col-lg-6 col-md-12">
    <div class="card-header">{{state.name}}</div>
    <div class="card-body">
      <h5 class="card-title">{{state.dueDate}}까지, {{state.successQ}}{{state.unit}}목표</h5>
      <p class="card-text">{{state.content}}</p>
      <a href="#" class="btn btn-success" @click="$router.go(-1)" >back</a>
    </div>
  </div>
  <div class="col-lg-3 col-md-12"></div>
</div>
</template>
<script>
import { ref,reactive } from '@vue/reactivity'
import { onMounted } from 'vue'
import {useRouter} from 'vue-router';
import axios from 'axios'

export default {
setup(){
  onMounted(()=> { getGoal()})
  const router = useRouter();
  const state = reactive({
    goalid    :'',
    name      :'',
    content   :'',
    successQ  :'',
    dueDate   :'',
    unit      :'',
    email     : sessionStorage.getItem('email'),
    token     : sessionStorage.getItem("TOKEN"),
  })
  const getGoal = async ()=>{
    console.log("GoalRead launched")
    const url= "http://43.201.60.125/api/goalread"
    const headers = {
      "Content-Type" : "application/json",
      Authorization: state.token,
      "token": state.token }
    const body = { goalid: state.goalid }
    console.log("state.goalid:: "+body.goalid)
    await axios.post(url, body, {headers}).then(function(res){
      if(res.status === 200){
        console.log("res.data.result::"+res.data.dto)
        state.name = res.data.dto.name
        state.content = res.data.dto.content
        state.successQ = res.data.dto.successQ
        state.dueDate = res.data.dto.dueDate
        state.unit = res.data.dto.unit
      }
    })
  }
  const deleteGoal = async ()=>{
    console.log("delete goal launched")
    const url= "http://43.201.60.125/api/goalDelete"
    const headers = {
      "Content-Type" : "application/json",
      Authorization: state.token,
      "token": state.token }
    const body = { goalid: state.goalid, }
    await axios.post(url, body, {headers}).then(function(res){
      if(res.status === 200){
        console.log("res.data.::"+res.data)
        alert("deleted "+state.name);router.push({name:"privateProfile"});
      }
    })
  }
return{getGoal,state, deleteGoal}
},
}
</script>

<style scoped>
img {
  width: 2em;
}
.btn{
  margin: 0.5em;
}
input:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
textarea:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
</style>
